@import "variables";
@import "mixins";

.modal-open {
	overflow: hidden!important;
}
.modal-backdrop {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 10;
	background-color: #0d0d0e;
	&.fade{opacity: 0;}
	&.in{opacity: .3;}
}
.modal {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 11;
	overflow: auto;
	text-align: center;
	padding: 10px;
	&:before{
		display: inline-block;
		vertical-align: middle;
		content: " ";
		height: 100%;
	}
}
.modal__dialog {
	width: 94%;
	z-index: 1050;
	//min-width: 300px;
	max-width: 1080px;

	display: inline-block;
	text-align: left;
	vertical-align: middle;
	background: #fff;
	padding: 28px 55px 10px 30px;
	.box-shadow();
	min-width: 768px;
	&-sm{
		max-width: 950px;
	}
	&-w320{
		min-width: 0;
		max-width: 320px;
	}
	&-w430{
		min-width: 0;
		max-width: 430px;
	}
	&-noBg{
		background: none;
		.box-shadow(none, none, none, none);
		padding: 0;
	}
}
.modal__bg{
	background: #fff;
	padding: 20px 55px 20px 30px;
	.box-shadow();
}
.modal__title {
	font-size: @fs-large;
	margin-bottom: 40px;
}
.modal__row{
	margin-bottom: 20px;
	&.mbLg{
		margin-bottom: 40px;
	}
}
.modal__heading{
	color: #a3a3a3;
	font-size: 0.6875rem;
	text-transform: uppercase;
	margin-bottom: 26px;
}
.modal__loaded{
	.modal__dialog{
		text-align: center;
		padding: 45px 20px 27px;
		color: #191a1b;
		font-size: 14px;
		line-height: 22px;
	}
	.btn{
		margin: 35px 10px 0;
		min-width: 95px;
		text-align: center;
	}
}
.modal__code{
	//position: absolute;
	//left: 0;
	//right: 0;
	//background: #fff;
	//.box-shadow();
	//top: -100px;
	//padding: 30px 60px 20px;
	overflow: hidden;
	.btn{
		float: right;
		margin-bottom: 0;
	}
	&Info{
		float: left;
		//margin-top: -11px;
	}
	&Label{
		color: #a7a7a7;
		font-size: 0.6875rem;
		margin: 0 0 10px;
	}
	&Text{
		color: #48484a;
		font-weight: 500;
		font-size: 0.8125rem;
	}
}
.editOwner{
	.modal__dialog{
		position: relative;
		//margin-top: 100px;
	}
	.customPlaceholder.filled{
		top: 17px!important;
	}
	.bottom__btns{
		margin-top: 40px;
	}
}
.corporate__box{
	max-height: 0;
	overflow: hidden;
}
.editHome {
	.customLabel {width: 150px;}
}
.editSettingsNoa{
	.modal__dialog {
		position: relative;
		//margin-top: 180px;
		margin-bottom: 180px;
	}
}
.modal__separate {
	position: absolute;
	left: 0;
	right: 0;
	background: #fff;
	-webkit-box-shadow: 0 0 10px #666;
	box-shadow: 0 0 10px #666;
	padding: 30px 60px 20px;
	&.h160{
		height: 160px;
		//top: -180px;
		bottom: -180px;
	}
	&Title{
		font-size: 20px;
		color: #000;
		margin-bottom: 9px;
	}
}

.modal__status{
	margin-bottom: 20px;
	&Icon{
		vertical-align: middle;
		padding-right: 10px;
		font-size: 18px;
		&.green{color: @brand-green;}
		&.yellow{color: @brand-yellow;}
		&.red{color: @brand-red;}
	}
	&Text{
		vertical-align: middle;
		font-size: 15px;
		font-weight: 500;
	}
}
.modal__list{
	text-align: left;
	margin: 15px 0 20px;
	list-style-type: disc;
	padding-left: 15px;
	&Title{
		font-size: 14px;
		margin: 15px 0 0;
		text-align: left;
	}
	&Item{
		margin-bottom: 7px;
		font-size: 13px;
		font-weight: 400;
	}
}